
@keyframes get-down {
    from{
        top: -126px;
    }
    to{
        top: 0;
    }
}

@keyframes get-up {
    from{
        top: 0;
    }
    to{
        top: -126px;
    }
}


@keyframes background-gradient {
    from{
        background-color: rgba(27, 22, 35, 1);
    }
    to{
        color: rgb(243, 250, 205);
        outline: 2px solid rgba(199, 199, 0, 0.6);
        text-shadow: 0 0 3px rgba(255, 255, 0, 0.3);
        box-shadow: 0 0 10px rgba(255, 255, 0, 0.6);
    }
}

@keyframes shake {
    0%{
        transform: rotate(0);
    }
    25%{
        transform: rotate(10deg);
    }
    50%{
        transform: rotate(-10deg);
    }
    100%{
        transform: rotate(0);
    }
}